  }

  .lg\:shadow-right {
    --tw-shadow: rgb(227, 0, 14, 0.4) 5px 5px, rgb(227, 0, 14, 0.3) 10px 10px,
      rgb(227, 0, 14, 0.2) 15px 15px, rgb(227, 0, 14, 0.1) 20px 20px,
      rgb(227, 0, 14, 0.05) 25px 25px;
    --tw-shadow-colored: 5px 5px var(--tw-shadow-color),
      10px 10px var(--tw-shadow-color), 15px 15px var(--tw-shadow-color),
      20px 20px var(--tw-shadow-color), 25px 25px var(--tw-shadow-color);
    box-shadow: var(--tw-ring-offset-shadow, 0 0 #0000),
      var(--tw-ring-shadow, 0 0 #0000), var(--tw-shadow);
  }
}

@media (min-width: 1280px) {
  .xl\:h-\[110vh\] {
    height: 110vh;
  }

  .xl\:px-48 {
    padding-left: 12rem;
    padding-right: 12rem;
  }
}
